@font-face {
  font-family: 'pf_din_text_cond_pro';
  src: url('../fonts/pfdintextcondpro-lightital-webfont.woff2') format('woff2'), url('../fonts/pfdintextcondpro-lightital-webfont.woff') format('woff');
  font-weight: 300;
  font-style: italic;
}
@font-face {
  font-family: 'pf_din_text_cond_pro';
  src: url('../fonts/pfdintextcondpro-medium-webfont.woff2') format('woff2'), url('../fonts/pfdintextcondpro-medium-webfont.woff') format('woff');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'pf_din_text_cond_pro';
  src: url('../fonts/pfdintextcondpro-regular-webfont.woff2') format('woff2'), url('../fonts/pfdintextcondpro-regular-webfont.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
* {
  margin: 0;
  padding: 0;
}
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}
ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
  border: none;
  color: #000;
}
a:hover {
  text-decoration: none;
}
body {
  width: 100%;
  margin: 0 auto;
  color: #000;
  font: 400 16px 'pf_din_text_cond_pro', sans-serif;
}
.width {
  width: 90%;
  margin: 0 auto;
}
h2 {
  font: 400 35px 'pf_din_text_cond_pro', sans-serif;
  color: #000;
  text-transform: uppercase;
  text-align: center;
}
h2.white {
  color: #fff;
}
h3 {
  font: 400 30px 'pf_din_text_cond_pro', sans-serif;
  color: #000;
  text-transform: uppercase;
  text-align: center;
}
h3.white {
  color: #fff;
}
.header {
  padding-top: 25px;
  padding-bottom: 40px;
}
.header__logo {
  float: left;
  margin-right: 145px;
}
.header__address {
  float: left;
  text-align: center;
  padding-top: 30px;
}
.header__address p {
  font: 400 19px 'pf_din_text_cond_pro', sans-serif;
}
.header__call {
  float: right;
  font: 400 30px 'pf_din_text_cond_pro', sans-serif;
  text-align: center;
  padding-top: 20px;
}
.header__call-back {
  text-transform: uppercase;
  font-size: 25px;
  margin-top: 5px;
}
.header__call-back a {
  background: #ff8400;
  color: #fff;
  padding: 5px 15px;
}
.header__call-back a:hover {
  background: #ff4e00;
}
.header__call-back a:active {
  background: #a53605;
}
@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .header__logo {
    margin-right: 70px;
  }
}
#header_menu {
  background: #ff8400;
  border-bottom: 1px solid #c46b0b;
  z-index: 1000;
}
nav {
  /*margin: 0;
    padding: 0;*/
}
.header_menu ul {
  text-align: justify;
  font-size: 0;
  padding: 15px 0;
}
.header_menu ul:after {
  display: inline-block;
  width: 100%;
  content: "";
  overflow: hidden;
}
.header_menu ul li {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  text-align: center;
  width: 14.28571428571429%;
}
.header_menu ul li:before {
  position: absolute;
  content: "â€¢";
  right: 0;
  color: #fff;
  font-size: 20px;
  line-height: 20px;
}
.header_menu ul li a {
  font: 400 20px 'pf_din_text_cond_pro', sans-serif;
  padding: 15px 10px;
  color: #fff;
  text-transform: uppercase;
}
.header_menu ul li a:hover {
  background: #ff4e00;
}
.header_menu ul li a:active {
  background: #a53605;
}
.header_menu li:last-child:before {
  display: none;
}
#main {
  overflow-x: hidden;
  overflow-y: hidden;
  border-top: 1px solid #fff;
  border-bottom: 4px solid #fdc029;
  position: relative;
  margin: 0 auto;
}
#main:before {
  position: absolute;
  content: "";
  border-bottom: 1px solid #fff;
  width: 100%;
  bottom: 1px;
  z-index: 1;
}
#main ul {
  text-align: left;
  font-size: 0;
  height: 405px;
  overflow: hidden;
  width: 115%;
  margin-left: -75px;
}
#main ul:after {
  width: 100%;
  display: inline-block;
  overflow: hidden;
  content: "";
}
#main li {
  display: inline-block;
  width: 16.666666666%;
  height: 405px;
  box-sizing: border-box;
  vertical-align: middle;
  transform: skewX(-20deg);
  overflow: hidden;
  border-left: 1px solid #fff;
}
#main li img {
  transform-origin: right top;
  transform: skewX(20deg) translate(-160px);
  height: 100%;
}
.main {
  margin: -205px auto 0 auto;
  width: 89.8989898989899%;
}
.main__form_block {
  background: rgba(255, 255, 255, 0.8);
  transform: skewX(-20deg);
  margin-bottom: 15px;
}
.form_wrap {
  transform: skewX(20deg);
  text-align: left;
  padding: 20px 0 20px 0;
}
.form_wrap h2 {
  text-align: center;
}
.form_wrap p {
  text-align: center;
  margin: 5px auto;
  text-transform: uppercase;
  font: 400 16px 'pf_din_text_cond_pro', sans-serif;
  color: #2b2a28;
}
.form {
  width: 100%;
  text-align: center;
}
.control,
.send_app {
  display: inline-block;
  vertical-align: middle;
}
.control input {
  width: 197px;
  height: 42px;
  padding: 10px 0 10px 10px;
  box-sizing: border-box;
  border: 1px solid #4e5659;
  margin-right: 5px;
  outline: none;
}
.control input:focus {
  border-color: yellow;
  box-shadow: 0 0 10px rgba(255, 136, 0, 0.6);
}
.control input.error {
  border-color: red;
  box-shadow: 0 0 10px rgba(255, 0, 0, 0.6);
}
.control input::placeholder {
  text-transform: uppercase;
  color: #2b2a28;
  font: 400 20px/20px 'pf_din_text_cond_pro', sans-serif;
}
.send_app input {
  border: 1px solid transparent;
  box-sizing: border-box;
  font: 500 25px/25px 'pf_din_text_cond_pro', sans-serif;
  height: 42px;
  padding: 0 45px;
  text-transform: uppercase;
  background: #ff8400;
}
.send_app input:hover {
  background: #ff4e00;
}
.send_app input:active {
  background: #a53605;
}
.main__footnote {
  text-align: right;
  margin-right: 30px;
  z-index: 1;
  margin-bottom: 17px;
  position: relative;
  color: #fff;
  font: 400 16px 'pf_din_text_cond_pro', sans-serif;
}
.main__footnote a {
  text-transform: uppercase;
  text-decoration: underline;
  color: #fff;
}
@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .main__form_block {
    margin-top: -290px;
    margin-bottom: 15px;
  }
  .control input {
    width: 100%;
  }
  .send_app input {
    padding: 0 30px;
  }
}
#services_equipment_products {
  margin-top: 60px;
  margin-bottom: 70px;
}
.services_wrap {
  float: left;
}
.services__list-item {
  margin-bottom: 45px;
}
.services__list-item img {
  box-sizing: border-box;
  float: left;
  width: 200px;
  height: 200px;
  border-radius: 250px;
  padding: 5px;
  margin-right: 70px;
  box-shadow: 0 0 5px 3px rgba(241, 241, 241, 0.7);
}
.services__list-item_cont {
  float: right;
  width: 435px;
  font: 400 18px 'pf_din_text_cond_pro', sans-serif;
}
.services__list-item_cont h3 {
  text-align: left;
  margin-bottom: 15px;
}
.services__list-item_cont p {
  margin-bottom: 25px;
}
.services__list-item_cont a {
  display: inline-block;
  font-size: 25px;
  color: #fff;
  text-transform: uppercase;
  padding: 10px 20px;
  background: #ff8400;
}
.services__list-item_cont a:hover {
  background: #ff4e00;
}
.services__list-item_cont a:active {
  background: #a53605;
}
.right_block {
  float: right;
}
.equipment_wrap {
  margin-bottom: 90px;
  margin: 0 auto 90px auto;
}
.equipment_wrap h2 {
  margin-bottom: 85px;
}
.equipment__list,
.products__list {
  text-align: center;
  position: relative;
}
.equipment__list ul,
.products__list ul {
  margin: 0 auto;
  margin-left: 13px;
}
.equipment__list li,
.products__list li {
  padding-top: 3px;
  width: 285px;
  margin: 0 auto;
}
.equipment__list img,
.products__list img {
  padding: 5px;
  box-shadow: 0 0 5px 3px rgba(241, 241, 241, 0.7);
  box-sizing: border-box;
  width: 275px;
  border-radius: 250px;
  height: 275px;
}
.products_wrap {
  width: 100%;
  margin: 0 auto;
}
.products_wrap h2 {
  margin-bottom: 85px;
}
.triangle_style {
  width: 0;
  height: 0;
  border-left: 9px solid transparent;
  border-right: 9px solid transparent;
  border-bottom: 17px solid #ff8400;
  position: absolute;
  cursor: pointer;
  left: 50%;
  margin-left: -10px;
}
.arrow_up {
  top: -47px;
}
.arrow_down {
  transform: rotate(180deg);
  bottom: -44px;
}
@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .services__list-item {
    width: 85%;
  }
  .services__list-item img {
    float: none;
  }
  .services__list-item_cont {
    float: none;
    width: 100%;
  }
  .right_block {
    float: right;
    width: 34%;
  }
  .equipment_wrap {
    margin-bottom: 200px;
  }
}
